// react redux
import React from "react";
import { useSelector } from "../../services/hooks";

// image
import vector1 from "../../images/vector1.svg";
import vector2 from "../../images/vector2.svg";
import vector3 from "../../images/vector3.svg";
import stroke from "../../images/stroke.svg";

//styles
import styles from "./order-details.module.css";

const OrderDetails = (): JSX.Element => {
  const { order } = useSelector((state) => state.orderReducer);

  return (
    <div className={`${styles.orderDetails} pt-20 pb-15`}>
      <h2 className={`${styles.title} text text_type_digits-large mb-8`}>
        {order.number}
      </h2>
      <p className="text text_type_main-medium"> </p>
      <div className={`${styles.image} mt-15 mb-15`}>
        <img src={vector1} className={`${styles.vector1}`} alt="" />
        <img src={vector2} className={`${styles.vector2}`} alt="" />
        <img src={vector3} className={`${styles.vector3}`} alt="" />
        <img src={stroke} className={`${styles.stroke} `} alt="" />
      </div>
      <p className="text text_type_main-default mb-2">
           
      </p>
      <p className="text text_type_main-default text_color_inactive">
            
      </p>
    </div>
  );
};

export default OrderDetails;
